
.beta-bar {
	position: fixed;
	bottom: 0;
	right: 0px;
	padding: 8px 0;
	z-index: 999;
	width: 200px;
	background-color: #4f81bd;
	color: #fff;
	text-align: center;
	transform: translate3d(60px,-24px,0) rotate(-45deg);
}
.beta-bar a{
	color: #fff;
	text-decoration: none;
}



